<template>
  <div>
    <div class='bar'>
      <el-divider direction="vertical"></el-divider>
      <div class='listItem' @click='routerChange("hollowList")'>
        <el-image ref='hollowIcon' :src='hollowIcon' style='width:40px'></el-image>
        <!-- <span>{{this.$t('Hollow.Title')}}</span> -->
      </div>
      <el-divider direction="vertical"></el-divider>
      <div class='listItem' @click='routerChange("marketList")'>
        <el-image ref='marketIcon' :src='marketIcon' style='width:40px'></el-image>
        <!-- <span>{{this.$t('Market.Title')}}</span> -->
      </div>
      <el-divider direction="vertical"></el-divider>
      <div class='listItem' @click='routerChange("newsList")'>
        <el-image ref='newsIcon' :src='newsIcon' style='width:40px'></el-image>
        <!-- <span>{{this.$t('News.Title')}}</span> -->
      </div>
      <el-divider direction="vertical"></el-divider>
      <div class='listItem' @click='routerChange("lafList")'>
        <el-image ref='lafIcon' :src='lafIcon' style='width:40px'></el-image>
        <!-- <span>{{this.$t('Laf.Title')}}</span> -->
      </div>
      <el-divider direction="vertical"></el-divider>
    </div>
  </div>
</template>

<script>
export default {
  name: 'frame-footer',
  data () {
    return {
      hollowIcon: require('@/../public/assets/hollow.png'),
      marketIcon: require('@/../public/assets/market.png'),
      newsIcon: require('@/../public/assets/news.png'),
      lafIcon: require('@/../public/assets/laf.png'),
    }
  },
  methods: {
    routerChange (target) {
      this.$bus.$emit('routerChange', target)
    },
    routerIconChange (target) {
      this.hollowIcon = require('@/../public/assets/hollow.png');
      this.marketIcon = require('@/../public/assets/market.png');
      this.newsIcon = require('@/../public/assets/news.png');
      this.lafIcon = require('@/../public/assets/laf.png');

      if (target == 'hollowList') {
        this.hollowIcon = require('@/../public/assets/hollow_filled.png');
      }
      else if (target == 'marketList') {
        this.marketIcon = require('@/../public/assets/market_filled.png');
      }
      else if (target == 'newsList') {
        this.newsIcon = require('@/../public/assets/news_filled.png');
      }
      else if (target == 'lafList') {
        this.lafIcon = require('@/../public/assets/laf_filled.png');
      }
    }
  },
  mounted () {
    this.routerIconChange(this.$route.name);
    this.$bus.$emit('routerIconChangeLeft', this.$route.name)
    this.$bus.$on('routerIconChangeFooter', this.routerIconChange);
  },
  beforeDestroy () {
    this.$bus.$off('routerIconChangeFooter');
  }
}
</script>

<style scoped>
.bar {
  background-color: rgb(255, 255, 255);
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.listItem {
  cursor: pointer;
  margin-top: 10px;
  text-align: center;
}
/* .listItem span {
  font-size: 25px;
  position: relative;
  top: -10px;
  margin-left: 5%;
} */
</style>